   for (let index = 0; index < theme.length; index++) {
     if(theme[index].classList.contains('v-night-theme')){
       theme[index].classList.remove('v-night-theme')
       theme[index].classList.add('v-light-theme')
       toggle[index].classList.remove('fa-toggle-on')
       toggle[index].classList.add('fa-toggle-off')
     }else if(theme[index].classList.contains('v-light-theme')){
      theme[index].classList.add('v-night-theme')
      theme[index].classList.remove('v-light-theme')
      toggle[index].classList.remove('fa-toggle-off')
       toggle[index].classList.add('fa-toggle-on')
    }
     
   }
