function loopClear(cln,r=true){
  // const ele = document.querySelectorAll(cln)
for (let index = 0; index < cln.length; index++) {
  if(r == true){cln[index].classList.add('hide')}else{
    cln[index].classList.remove(r)
  }
  
}

}
const toggleMenu = (x,d = false)=>{
  
  const drop = document.querySelectorAll(".v-menu-expandable");
  const wrap = document.querySelectorAll(".v-menu-btn-wrapper")
  const dropBtn = document.querySelectorAll(".v-menu-drop-btn");
  const expand = document.querySelectorAll('.v-menu-expandable-btn')

  if(d == false){
    loopClear(dropBtn,'v-pender')
    loopClear(drop)
    dropBtn[x].classList.add('v-pender')
    drop[x].classList.remove("hide")
  }else if(d == true){
    loopClear(dropBtn,'v-pender')
    loopClear(expand,"v-wrap-active")
    loopClear(wrap,"v-menu-btn-active")
    loopClear(drop)
    wrap[x].classList.add("v-menu-btn-active") 
  }else if(d === "exp"){
    loopClear(expand,"v-wrap-active")
    loopClear(wrap,"v-menu-btn-active")
    expand[x].classList.add('v-wrap-active')
  }
}


